import { useCallback } from 'react';
import T from 'prop-types';
import styled from '@emotion/styled';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import FavoriteIcon from '@mui/icons-material/Favorite';

const ItemContent = styled(CardContent)`
  padding: 1rem 1.5rem;
`;

const ItemHeader = styled(CardHeader)`
  & .MuiCardHeader-title {
    font-weight: bold;
  }
`;

const ItemActions = styled(CardActions)`
  position: relative;
  padding: 1rem;
`;

const HeartIcon = styled(FavoriteIcon, {
  shouldForwardProp: () => false,
})`
  color: ${(props) => (props.liked ? '#ED4956' : '')}
`;

const GalleryItem = ({ item, onItemLiked }) => {
  const handleLike = useCallback(() => {
    onItemLiked(item.id);
  }, [item, onItemLiked]);

  const mediaType = item.media_type === 'video' ? 'iframe' : 'img';

  return (
    <Card>
      <CardMedia component={mediaType} src={item.url} />
      <ItemActions>
        <IconButton aria-label="like the post" onClick={handleLike}>
          <HeartIcon fontSize="large" liked={!!item.meta.liked} />
        </IconButton>
      </ItemActions>
      <ItemHeader title={item.title} subheader={`Date: ${item.date}`} />

      <ItemContent>
        <Typography align="left">{item.explanation}</Typography>
        <br />
        <Typography align="left" color="text.primary">{`© ${item.copyright || 'unknown'}`}</Typography>
      </ItemContent>
    </Card>
  );
};

GalleryItem.propTypes = {
  item: T.shape({
    id: T.string.isRequired,
    title: T.string.isRequired,
    media_type: T.string.isRequired,
    date: T.string.isRequired,
    url: T.string.isRequired,
    explanation: T.string.isRequired,
    copyright: T.string,
    meta: T.shape({
      liked: T.bool,
    }),
  }).isRequired,
  onItemLiked: T.func.isRequired,
};

export default GalleryItem;
